<script>
import baidumap from "@/components/baidumap";

export default {
  name: "newWorld",
  components:{
    baidumap
  },
  methods:{
    change(){
      var div=document.getElementById("goods_img");
      var arr_img=div.getElementsByTagName("img");
      for(var i=0;i<arr_img.length;i++){
        arr_img[i].onmouseover=function(){
          var bigimg=document.getElementById("bigimg");
          var p=this.src;
          console.log(this.src,bigimg.src)
          try {
            bigimg.src=p.replace(".png","_big.png");
          }catch (e) {
            bigimg.src=p.replace(".jpg","_big.jpg");
          }

          // bigimg.src=p.replace(".png","_big.png");
        }
      }
    },
    change1(){
      let timerID;
      let bannerIndex = 0;//0
      let containner = document.querySelector('.containner');//

      let right = document.getElementById('right');
      right.onclick = function () {
        next();
        animate();
      }

      let left = document.getElementById('left');
      left.onclick = function () {
        prev();
        animate();
      }

      //
      autoPlay();

      //
      let banner = document.querySelector('.teacher-p');
      mouseJoin(banner);
      mouseLeave(banner);



      mouseJoin(left);
      mouseLeave(left);
      mouseJoin(right);
      mouseLeave(right);
      // var timerID;


      //
      function next() {
        console.log(bannerIndex,containner.children.length)
        if (bannerIndex > (containner.children.length-4)) {//(containner.children.length-3)/33
          containner.style.transition = 'none';//
          bannerIndex = 0;//
          containner.style.transform = 'translateX(0px)';//containner
        }
        bannerIndex++;
      }

      //
      function prev() {
        if (bannerIndex <= 0) {
          containner.style.transition = 'none';//
          // bannerIndex = (containner.children.length-3)/3;//3 
          bannerIndex = containner.children.length - 3;//3 
          let moveX = bannerIndex * 400;//
          containner.style.transform = `translateX(-${moveX}px)`;//
        }
        bannerIndex--;
      }

      //
      function animate() {
        setTimeout(function () {
          containner.style.transition = 'transform 0.5s linear';//
          // let moveX = bannerIndex*1200;
          let moveX = bannerIndex * 400;
          //ES6
          containner.style.transform = `translateX(-${moveX}px)`;//
        }, 1);
      }

      //
      function autoPlay() {
        timerID = setInterval(function () {//
          right.click();//
        }, 3000);
      }

      //
      function mouseJoin(obj) {
        obj.onmouseenter = function () {
          clearInterval(timerID);
        }
      }

      //
      function mouseLeave(obj) {
        //
        obj.onmouseleave = function () {
          clearInterval(timerID);
          timerID = setInterval(function () {
            right.click();
          }, 3000);
        }
      }
    }
  },
  mounted() {
    this.change()
    this.change1()
  }

}
</script>

