<template>
  <div class="home">
    <div id="container"
    :style="{width:width+'px',height:height+'px'}">
    </div>
  </div>
</template>

<script>

export default {
  name: "baidumap",
  data() {
    return {
      width: 282,
      height: 282,
      map: null,
    };
  },

  mounted() {
    //
    this.map = new BMap.Map('container');
    /**/
    let point = new BMap.Point(113.381391, 23.14641);
    /**/
    this.map.centerAndZoom(point, 18);
    /**/
    this.map.enableScrollWheelZoom(true);
  }
};
</script>

<style scoped>
#allmap {
  width: 282px ;
  height: 282px;
}
</style>